import app from 'flarum/admin/app';
import EditTagModal from 'flarum/tags/components/EditTagModal';
import { extend } from 'flarum/common/extend';
import Stream from 'flarum/utils/Stream';
import Model from 'flarum/Model';
import Tag from 'flarum/tags/models/Tag';

export default function () {
  if (app.initializers.has('flarum-tags')) {
    Tag.prototype.isChatgpt = Model.attribute('isChatgpt');
    extend(EditTagModal.prototype, 'oninit', function () {
      this.isChatgpt = Stream(this.tag.isChatgpt() || false);
    });

    extend(EditTagModal.prototype, 'fields', function (items) {
      items.add(
        'chatgpt',
        <div className="Form-group">
          <div>
            <label className="checkbox">
              <input type="checkbox" bidi={this.isChatgpt} />
              {app.translator.trans('datlechin-chatgpt.admin.edit_tag.chatgpt_label')}
            </label>
          </div>
        </div>,
        10
      );

    });

    extend(EditTagModal.prototype, 'submitData', function (data) {
      data.isChatgpt = this.isChatgpt();
    });
  }
}
